import ToDoList from "./ToDoList";
import {connect} from "react-redux";
import {
    addTodoActionCreator,
    deleteAllCompleteTodoActionCreator,
    deleteTodoActionCreator,
    filterTodoActionCreator,
    toggleAllTodoActionCreator,
    updateTodoStatusActionCreator,
    updateTodoTextActionCreator
} from "../../redux/todoList-reducer";

let mapStateToProps = (state) => {
   return {
       todoList: state.todoList,
       newText: state.todoList.newText,
       todos: state.todoList.todos,
       filter: state.todoList.filter
   }
}

let mapDispatchToProps = (dispatch) => {
    return {
        addTodo: () => {
            dispatch(addTodoActionCreator())
        },
        deleteAllCompleteTodo: () => {
            dispatch(deleteAllCompleteTodoActionCreator())
        },
        toggleAllTodo: () => {
            dispatch(toggleAllTodoActionCreator())
        },
        deleteTodo: (id) => {
            dispatch(deleteTodoActionCreator(id))
        },
        updateTodoText: (text) => {
            dispatch(updateTodoTextActionCreator(text))
        },
        updateTodoStatus: (id) => {
            dispatch(updateTodoStatusActionCreator(id))
        },
        filterTodos: (filterText) => {
            dispatch(filterTodoActionCreator(filterText))
        }
    }
}

const ToDoListContainer = connect(mapStateToProps, mapDispatchToProps) (ToDoList);

export default ToDoListContainer;