const ADD_POST = 'addPost';
const UPDATE_NEW_POST_TEXT = 'updateNewPostText';

const UPDATE_PROFILE_NAME = 'updateProfileName';
const UPDATE_PROFILE_BIRTH_DATE = 'updateProfileBirthDate';
const UPDATE_PROFILE_CITY = 'updateProfileCity';
const UPDATE_PROFILE_EDUCATION = 'updateProfileEducation';
const UPDATE_PROFILE_WEBSITE = 'updateProfileWebsite';

let initialState = {
    posts: [
        {
            id: 1,
            message: 'Hi, how are you?',
            likesCount: 12
        },
        {
            id: 2,
            message: 'It\'s my first post',
            likesCount: 1
        },
    ],
    newPostText: '',
    profileInfo: {
        name: 'Mykola',
        birthDate: '16.12.1992',
        city: 'Kamyanets-Podilskiy',
        education: 'React Developer',
        website: 'skoda-club.ua',
        toggleButtonText: 'изменить',
    },
}

const profileReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_POST:
            return {
                ...state,
                newPostText: '',
                posts: [...state.posts, {
                    id: 3,
                    message: state.newPostText,
                    likesCount: 0
                }]
            }

        case UPDATE_NEW_POST_TEXT:
            return {
                ...state,
                newPostText: action.newText
            }

        case UPDATE_PROFILE_NAME:
            return {
                ...state,
                profileInfo: {
                    ...state.profileInfo,
                    name: action.newText
                }
            }

        case UPDATE_PROFILE_BIRTH_DATE:
            return {
                ...state,
                profileInfo: {
                    ...state.profileInfo,
                    birthDate: action.newText
                }
            }

        case UPDATE_PROFILE_CITY:
            return {
                ...state,
                profileInfo: {
                    ...state.profileInfo,
                    city: action.newText
                }
            }

        case UPDATE_PROFILE_EDUCATION:
            return {
                ...state,
                profileInfo: {
                    ...state.profileInfo,
                    education: action.newText
                }
            }

        case UPDATE_PROFILE_WEBSITE:
            return {
                ...state,
                profileInfo: {
                    ...state.profileInfo,
                    website: action.newText
                }
            }

        default:
            return state;
    }
}

export const addPostActionCreator = () => ({
    type: ADD_POST,
});
export const updateNewPostTextActionCreator = (text) => ({
    type: UPDATE_NEW_POST_TEXT,
    newText: text
});

export const updateProfileName = (text) => ({
    type: UPDATE_PROFILE_NAME,
    newText: text
});
export const updateProfileBirthDate = (text) => ({
    type: UPDATE_PROFILE_BIRTH_DATE,
    newText: text
});
export const updateProfileCity = (text) => ({
    type: UPDATE_PROFILE_CITY,
    newText: text
});
export const updateProfileEducation = (text) => ({
    type: UPDATE_PROFILE_EDUCATION,
    newText: text
});
export const updateProfileWebsite = (text) => ({
    type: UPDATE_PROFILE_WEBSITE,
    newText: text
});

export default profileReducer;