import React from 'react';
import ToDo from "./ToDo";

const ToDoList = (props) => {

    let handleSubmit = e => {
        e.preventDefault();
        props.addTodo();
    }

    let handleDelete = (e) => {
        props.deleteTodo(e.target.id);
    }

    let deleteAllComplete = () => {
        props.deleteAllCompleteTodo()
    }

    let toggleAll = () => {
        props.toggleAllTodo()
    }

    let handleToggle = (e) => {
        props.updateTodoStatus(e.target.id);
    }

    let handleChange = e => {
        props.updateTodoText(e.target.value);
    }

    let handleFilter = e => {
        props.filterTodos(e.target.value);
    }

    let todos = [];

    if (props.filter === 'all') {
        todos = props.todos
    } else if (props.filter === 'active') {
        todos = props.todos.filter(todo => !todo.complete)
    } else if (props.filter === 'complete') {
        todos = props.todos.filter(todo => todo.complete)
    }

    let todosLeft = props.todos.filter(todo => !todo.complete).length

    let someCompleteTodos = props.todos.some(todo => todo.complete)

    let someTodos = props.todos.some(todo => todo)

    let toDoElements = todos
        .map(t => <ToDo key={t.id} id={t.id} text={t.text}
                        completeToggle={t.complete}
                        handleComplete={handleToggle}
                        handleDelete={handleDelete}
        />)

    let newTodoText = props.newText;

    return (
        <div className="ToDoWrapper">
            <form action="" onSubmit={handleSubmit}>
                <div className="inputBlock">
                    <input type="text" name="todoAdd" value={newTodoText} onChange={handleChange}/>
                    <button onClick={handleSubmit}>add todo</button>
                </div>
            </form>
            {toDoElements}
            <p className="todosLeft">Todos Left: {todosLeft}</p>
            <div className="filterButtons">
                <button onClick={handleFilter} value="all"> Show all</button>
                <button onClick={handleFilter} value="active">Show active</button>
                <button onClick={handleFilter} value="complete">Show complete</button>
            </div>
            <div>
                {someTodos ? <button onClick={toggleAll}>toggle all todos</button> : ''}
            </div>
            <div>
                {someCompleteTodos ? <button onClick={deleteAllComplete}>delete all complete todos</button> : ''}
            </div>
        </div>
    )
}

export default ToDoList;