import * as shortid from "shortid";

const ADD_TODO = 'addTodo';
const DELETE_TODO = 'deleteTodo';
const DELETE_ALL_COMPLETE_TODO = 'deleteAllCompleteTodo';
const TOGGLE_ALL_TODO = 'toggleAllTodo';
const UPDATE_TODO_TEXT = 'updateTodoText';
const UPDATE_TODO_STATUS = 'updateTodoStatus';
const FILTER_TODO = 'filterTodo';

let initialState = {
    todos: [],
    newText: '',
    filter: 'all',
}

const todoListReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_TODO:
            if (state.newText.length > 2) {
                return {
                    ...state,
                    newText: '',
                    todos: [{id: shortid.generate(), text: state.newText, complete: false}, ...state.todos],
                };
            }
            return state

        case UPDATE_TODO_TEXT:
            return {
                ...state,
                newText: action.newText
            };

        case UPDATE_TODO_STATUS:
            return {
                ...state,
                todos: [...state.todos].map((todo) => {
                    if (todo.id === action.id) {
                        return {
                            ...todo,
                            complete: !todo.complete
                        }
                    } else {
                        return todo
                    }
                })
            }

        case FILTER_TODO:
            return {
                ...state,
                filter: action.status
            }

        case DELETE_TODO:
            return {
                ...state,
                todos: state.todos.filter(todo => todo.id !== action.id),
            }

        case DELETE_ALL_COMPLETE_TODO:
            return {
                ...state,
                todos: state.todos.filter(todo => !todo.complete),
            }

        case TOGGLE_ALL_TODO:
            return {
                ...state,
                todos: [...state.todos].map((todo) => {
                    return {
                        ...todo,
                        complete: !todo.complete
                    }
                })
            }

        default:
            return state;
    }
}

export const addTodoActionCreator = () => ({
    type: ADD_TODO
});

export const deleteAllCompleteTodoActionCreator = () => ({
    type: DELETE_ALL_COMPLETE_TODO
});

export const toggleAllTodoActionCreator = () => ({
    type: TOGGLE_ALL_TODO
});

export const deleteTodoActionCreator = (id) => ({
    type: DELETE_TODO,
    id: id
});

export const updateTodoTextActionCreator = (todoText) => ({
    type: UPDATE_TODO_TEXT,
    newText: todoText
});

export const updateTodoStatusActionCreator = (id) => ({
    type: UPDATE_TODO_STATUS,
    id: id
});

export const filterTodoActionCreator = (filterText) => ({
    type: FILTER_TODO,
    status: filterText
});

export default todoListReducer;