import './App.css';
import React, {useEffect, useState} from 'react';
import {Route} from "react-router-dom";
import Header from "./components/Header/Header";
import Profile from "./components/Profile/Profile";
import Settings from "./components/Settings/Settings";
import Music from "./components/Music/Music";
import News from "./components/News/News";
import DialogsContainer from "./components/Dialogs/DialogsContainer";
import Navbar from "./components/Navbar/Navbar";
import ToDoListContainer from "./components/ToDoList/ToDoListContainer";

const App = () => {
    let pageWrapper = React.createRef();
    let [bgColor, setColor] = useState('#14151a');

    useEffect(() => {
        pageWrapper.current.style.backgroundColor = bgColor
    });

    return (
        <main className="pageWrapper" ref={pageWrapper}>
            <div className="container">
                <div className="appWrapper">
                    <Header/>
                    <Navbar changeBgColor={setColor}/>
                    <div className="appContent">
                        <Route path="/dialogs" render={() => <DialogsContainer/>}/>
                        <Route path="/profile" render={() => <Profile/>}/>
                        <Route path="/news" render={() => <News/>}/>
                        <Route path="/music" render={() => <Music/>}/>
                        <Route path="/settings" render={() => <Settings/>}/>
                        <Route path="/todolist" render={() => <ToDoListContainer/>}/>
                    </div>
                </div>
            </div>
        </main>
    );
}

export default App;