import React from 'react';
import s from './ProfileInfo.module.css';

const ProfileInfo = (props) => {
    let state = props.profileInfo;

    let onInputValueUpdate = (el) => {
        let text = el.target.value,
            inputName = el.target.name;

            props.updateInputValue(text, inputName);
    }

    let changeValue = (el) => {
        let inputParent = el.target.parentElement,
            input = inputParent.querySelector('input'),
            elObject = el.target;

        if (input.className !== 'input edit') {
            input.className = 'input edit';
            elObject.innerText = 'сохранить';
        } else {
            input.className = 'input';
            elObject.innerText = 'изменить';
        }
    }

    return (
        <div className={s.profileInfo}>
            <div className={s.thumbImg}>
                <a href="#s" className={s.logo}>
                    <img src="https://miro.medium.com/max/1000/0*wZAcNrIWFFjuJA78" alt=""/>
                </a>
            </div>
            <div className={s.authorInfo}>
                <div className={s.avatar}>
                    <img src="https://miro.medium.com/max/1000/0*wZAcNrIWFFjuJA78" alt=""/>
                </div>
                <div className="infoBlock">
                    <div className={s.blockItem}>
                        <input type="text" className="input" name="name" onChange={onInputValueUpdate}
                               value={state.name}/>
                        <button className="edit" onClick={changeValue} type="button">изменить</button>
                    </div>
                    <div className={s.infoList}>
                        <div className={s.blockItem}>
                            <input type="text" className="input" name="birthDate" onChange={onInputValueUpdate}
                                   value={state.birthDate}/>
                            <button className="edit" onClick={changeValue} type="button">изменить</button>
                        </div>
                        <div className={s.blockItem}>
                            <input type="text" className="input" name="city" onChange={onInputValueUpdate}
                                   value={state.city}/>
                            <button className="edit" onClick={changeValue} type="button">изменить</button>
                        </div>
                        <div className={s.blockItem}>
                            <input type="text" className="input" name="education" onChange={onInputValueUpdate}
                                   value={state.education}/>
                            <button className="edit" onClick={changeValue} type="button">изменить</button>
                        </div>
                        <div className={s.blockItem}>
                            <input type="text" className="input" name="website" onChange={onInputValueUpdate}
                                   value={state.website}/>
                            <button className="edit" onClick={changeValue} type="button">изменить</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}
export default ProfileInfo;