let mapStateToProps = (state) => {
    return {
        profileInfo: state.profilePage.profileInfo
    }
}

let mapDispatchToProps = (dispatch) => {
    return {
        updateInputValue: (text, inputName) => {
            switch (inputName) {
                case 'name':
                    dispatch(updateProfileName(text))
                    break
                case 'birthDate':
                    dispatch(updateProfileBirthDate(text))
                    break
                case 'city':
                    dispatch(updateProfileCity(text))
                    break
                case 'education':
                    dispatch(updateProfileEducation(text))
                    break
                case 'website':
                    dispatch(updateProfileWebsite(text))
                    break
                default:
                    return
            }
        }
    }
}

const ProfileInfoContainer = connect(mapStateToProps, mapDispatchToProps) (ProfileInfo);

export default ProfileInfoContainer;