import React from 'react';

const ToDo = (props) => {
    return (
        <div className="toDoBlock">
            <input type="checkbox" name="toDo" id={props.id} onChange={props.handleComplete} checked={props.completeToggle}/>
            <label htmlFor={props.id} className={`${props.completeToggle}`}>{props.text}</label>
            <button onClick={props.handleDelete} id={props.id}>&times;</button>
        </div>
    )
}

export default ToDo;